
.toon-loadmore {
    overflow: hidden
}
.toon-loadmore__content {}
.toon-loadmore__content.is-dropped {
    transition: .2s
}
.toon-loadmore__top, .toon-loadmore__bottom {
    text-align: center;
    height: 50px;
    line-height: 50px
}
.toon-loadmore__top {
    margin-top: -50px
}
.toon-loadmore__bottom {
    margin-bottom: -50px
}
.toon-loadmore__spinner {
    display: inline-block;
    margin-right: 5px;
    vertical-align: middle
}
.toon-loadmore__text {
    vertical-align: middle
}



