
.tn-checklist {
    background: #fff;
}
.tn-checklist .tn-cell {
    padding: 0;
}
.tn-checklist.is-limit .tn-checkbox__core:not(:checked) {
    background-color: #ccc;
    border-color: #ccc;
}
.tn-checklist__label {
    display: block;
    padding:10px 0 10px 0;
    margin-left:10px;
    position: relative;
}
.tn-checklist__label:before {
    content: '';
    position: absolute;
    z-index: 2;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background-image: linear-gradient(0deg, #e4e4e4 50%, transparent 50%);
}
.tn-checklist__title {
    color: #999;
    display: block;
    font-size: 12px;
    margin: 8px;
}
.tn-checkbox {}
.tn-checkbox.is-right {
    float: right;
}
.tn-checkbox__label {
    vertical-align: middle;
    margin-left: 6px;
}
.tn-checkbox__input {
    display: none;
}
.tn-checkbox__input:checked + .tn-checkbox__core {
    background-color: #26a2ff;
    border-color: #26a2ff;
}
.tn-checkbox__input:checked + .tn-checkbox__core::after {
    /*background:#fff;*/
    border-color: #fff;
    -ms-transform: rotate(45deg) scale(1);
        transform: rotate(45deg) scale(1);
}
.tn-checkbox__input[disabled] + .tn-checkbox__core {
    background-color: #ccc;
    border-color: #ccc;
}
.tn-checkbox__core {
    display: inline-block;
    background-color: #fff;
    border-radius: 100%;
    border: 1px solid #ccc;
    position: relative;
    width: 20px;
    height: 20px;
    vertical-align: middle;
}
.tn-checkbox__core::after {
    border: 2px solid transparent;
    border-left: 0;
    border-top: 0;
    content: " ";
    top: 4px;
    left: 7px;
    position: absolute;
    width: 4px;
    height: 8px;
    -ms-transform: rotate(45deg);
        transform: rotate(45deg);
    transition: transform .2s;
}



