
.page-lazyload {}
.page-lazyload__list {
    text-align: center
}
.page-lazyload__listitem {
    width: 300px;
    margin: 0 auto;
    margin-bottom: 10px;
    background-color: #ddd
}
.page-lazyload__image {
    display: block;
    width: 100%
}
.page-lazyload__image[lazy=loading] {
    width: 40px;
    height: 300px;
    margin: auto
}



