
.m-radio{
  overflow: hidden;
  background: #fff;
  padding-left:15px;
}
.m-radio li{
  height: 44px;
  position: relative;
}
.m-radio li label{
  display: block;
  overflow: hidden;
}
.m-radio li:before{
  content: '';
  position: absolute;
  z-index: 2;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-image: linear-gradient(0deg, #e4e4e4 50%, transparent 50%);
}
.m-radio li:last-child{
  border-bottom: none;
}
.m-radio-val{
  height: 44px;
  width: 28px;
  float: left;
  display: block;
}
.m-radio-val input{
  display: none;
}
.m-radio-label{
  float: left;
  font-size: 18px;
  line-height: 44px;
}
.m-radio-label-in{
  color: #3395ff;
}
.m-radio-label-disabled{
  color: #999;
}
.is-right{
  float: right;
}
.in{
  background-image: url("data:image/png;base64,");
  background-repeat: no-repeat;
  background-position: left center;
  background-size: 13px;
}



